.dock-outer {
  margin: 0 0.5rem;
  display: flex;
  max-width: 100%;
  align-items: center;
  background: transparent;
}

.dock-panel {
  position: absolute;
  bottom: 0.5rem;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  align-items: flex-end;
  width: fit-content;
  gap: 1rem;
  border-radius: 1rem;
  background: transparent;
  padding: 0 0.5rem 0.5rem;
}

/* Mobile responsive adjustments */
@media (max-width: 768px) {
  .dock-panel {
    gap: 0.4rem;
    padding: 0 0.2rem 0.2rem;
    bottom: 0.2rem;
  }
  
  .dock-outer {
    margin: 0 0.2rem;
  }
}

.dock-item {
  position: relative;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  border-radius: 10px;
  background-color: #18213d;
  border: 1px solid #2b2b2b;
  box-shadow: none;
  cursor: pointer;
  outline: none;
}

.dock-icon {
  display: flex;
  align-items: center;
  justify-content: center;
 
  opacity: 1; /* make icons semi-transparent */
  transition: opacity 0.3s ease;
}

.dock-item:hover .dock-icon {
  opacity: 1; /* fully opaque on hover */
}



.dock-label {
  position: absolute;
  top: -1.5rem;
  left: 50%;
  width: fit-content;
  white-space: pre;
  border-radius: 0.375rem;
  border: 1px solid #222;
  background-color: #060010;
  padding: 0.125rem 0.5rem;
  font-size: 0.75rem;
  color: #fff;
  transform: translateX(-50%);
}